<script>
  export default{
    name:'toast',
    data(){
      return {
        alertTime:null,
        toastText:'',
      }
    },
    methods:{
      alertMesFn(mes){
        this.toastText=mes
        if(this.alertTime){
          clearTimeout(this.alertTime)
        }
        this.alertTime=setTimeout(()=>{
          this.toastText=''
          this.alertTime=null
        },2000)
      }
    }
  }
</script>

