import Vue from "vue"
import App from "./App.vue"
import router from "./router"
import store from "./store"

import 'we-vue/lib/style.css'

import "lib-flexible"
import FastClick from "fastclick"
import VueClipboard from 'vue-clipboard2'

Vue.use(VueClipboard)

// require('./assets/js/browser.js')

FastClick.attach(document.body);

// 修复ios下input，textarea聚焦不灵敏的问题
FastClick.prototype.focus = function (targetElement) {
  let length;
  try{
    if (targetElement.setSelectionRange && targetElement.type.indexOf('date') !== 0 && targetElement.type !== 'time' && targetElement.type !== 'month') {
      length = targetElement.value.length;
      targetElement.focus();
      targetElement.setSelectionRange(length, length);
    } else {
      targetElement.focus();
    }
  }catch(err){
    
  }
}
Vue.config.productionTip = false;

import Vconsole from 'vconsole';
if(process.env.VUE_APP_PLATFORM!=='prd'){
  Vue.prototype.$vConsole = new Vconsole();
}

import { VueJsonp } from 'vue-jsonp'    // 网上很多博客引用不加{}，会报错

Vue.use(VueJsonp)

import Toasts from './components/toast/index.vue'
const Toast = {
  install: function (Vue) {
    // 创建一个Vue的“子类”组件
    const ToastConstructor = Vue.extend(Toasts)
    // 创建一个该子类的实例,并挂载到一个元素上
    const instance = new ToastConstructor()
    // 将这个实例挂载到动态创建的元素上,并将元素添加到全局结构中
    instance.$mount(document.createElement('div'))
    document.body.appendChild(instance.$el)

    // 在Vue的原型链上注册方法，控制组件
    Vue.prototype.$Toast = (msg) => {
      instance.alertMesFn(msg);
    }
  }
}
Vue.use(Toast)

import Loading from './components/loading/index.vue'
const myloading = {
  install: function (Vue) {
    // 创建一个Vue的“子类”组件
    const ToastConstructor = Vue.extend(Loading)
    // 创建一个该子类的实例,并挂载到一个元素上
    const instance = new ToastConstructor()
    // 将这个实例挂载到动态创建的元素上,并将元素添加到全局结构中
    instance.$mount(document.createElement('div'))
    document.body.appendChild(instance.$el)

    // 在Vue的原型链上注册方法，控制组件
    Vue.prototype.$LoadingShow = (msg) => {
      instance.show(msg);
    }
    Vue.prototype.$LoadingHide = () => {
      instance.hide();
    }
  }
}
Vue.use(myloading)

import closePage from './components/closePage/index.vue'
const myclose = {
  install: function (Vue) {
    const ToastConstructor = Vue.extend(closePage)
    const instance = new ToastConstructor()
    instance.$mount(document.createElement('div'))
    document.body.appendChild(instance.$el)
    Vue.prototype.$closeHide = () => {
      instance.close();
    }
    Vue.prototype.$closeShow = () => {
      instance.show();
    }
  }
}
Vue.use(myclose)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
