<template>
  <div v-if='isClose' class="credentials-bg hiddenbg tc">
    <div class="credentials-bg-main hiddenbg_div">
      <div class="credentials-bg-top">
        <div class="text3">{{text ? text :'您在此页面停留过久，数据已失效'}}</div>
      </div>
      <div class="flex font-bold toast-mybtn">
        <div @click.stop="close" class="munual-btn2">关闭</div>
      </div>
    </div>
  </div>
</template>

<script>
  import bridge from 'fetch/bridge.js'
  export default{
    name:'closePage',
    data(){
      return {
        isClose:false,
        text:'',
      }
    },
    methods:{
      show(text){
        this.isClose=true;
        this.text=text
      },
      close(){
        let href=window.location.href;
        if(href.indexOf('type=app')>=0){
          bridge('finishPage')
        }else{
          if(href.indexOf('clientType=wechatMini')>=0 || sessionStorage.isminiType){
            wx.miniProgram.navigateBack({delta:1})
          }else{
            if (typeof WeixinJSBridge==='object'){
              document.addEventListener('WeixinJSBridgeReady', function(){ WeixinJSBridge.call('closeWindow'); }, false);
              WeixinJSBridge.call('closeWindow');
            }else{
              window.opener=null;
              window.open('about:blank','_self','').close()
            }
          }
        }
        this.text=''
      }
    }
  }
</script>

<style scoped lang='scss'>
  @import '~css/noticeToast.scss';
</style>
