//修改成你所需要定义的值
const funKey='function_name';
const jsonParams='jsonParams';
let bridge='iBuickNewEnergyMessage'
if(browser.versions.android){
  bridge="IBUser"
}

function callMethod(callback='',winName=''){
  window[winName] = (data=>{
	  console.log('callMethod方法==',{data},typeof data)
    if(typeof data =='string'){
		let str=data.replace(/(\n|\r|\\n|\\\\n)/g,'<br/>')
		console.log({str})
		let d=JSON.parse(str);
		if(d.resultCode=='1090'){
			d.message=d.message.replace(/\d+-\d+-\d+/g,function($1,$2){
				console.log({$1,$2})
				return `<a style="color:#005FBE;" href="tel:${$1}">${$1}</a>`
			})
			console.log('333===',{d})
		}
		data=d;
		console.log({data,d})
      //data=JSON.parse(data)
    }
    callback && callback(data)
  });
  console.log('这里是全局方法=',window[winName],winName)
}
/*
*params funName 桥接函数名
*params 桥接的传递的参数
*params callback 回调函数
*params winName 请求数据成功后app调用的全局方法名
*/
export default function jsBridge(funName,params='',{callback='',winName='',bridgeName='',isJump=false}={}){
  if(winName){
    callMethod(callback,winName)
  }
  bridgeName=bridgeName ? bridgeName : bridge;
  //console.log('bridgeName==',bridgeName)
  if(navigator.onLine){
    params=params ? params : '';
    if(typeof params !=='string'){
      params=JSON.stringify(params)
    }
    console.log('params=',params)
    if(funName){
      if(browser.versions.android){
        if(params){
          console.log('00000',window[bridgeName][funName])
          window[bridgeName][funName](params);
        }else{
          console.log('111111',window[bridgeName][funName],bridgeName,funName)
          window[bridgeName][funName]();
        }
      }else if(browser.versions.iPhone){
        //console.log(window.webkit.messageHandlers[bridgeName],bridgeName)
        
        if(funName=='getCarVerifyInfo'){
          console.log({[funKey]:funName,vin:params})
          window.webkit.messageHandlers[bridgeName].postMessage({[funKey]:funName,vin:params})
        }else if(funName=='goCarCertifitionPage' && isJump){
          console.log({[funKey]:funName,jumpUrl:params})
          window.webkit.messageHandlers[bridgeName].postMessage({[funKey]:funName,jumpUrl:params})
        }
        else{
          console.log({[funKey]:funName,[jsonParams]:params})
          window.webkit.messageHandlers[bridgeName].postMessage({[funKey]:funName,[jsonParams]:params})
        }
        
      }else{
        console.log('不是手机')
      }
    }else{
      console.log('请传入桥接的app的function_name')
    }
  }
}