import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);
let state={
  loading:false,
  imgFile:null,
  certCardMes:{},//三要素的图片信息
  
}
export default new Vuex.Store({
  state,
  getters:{
    getLoading:state=>state.loading
  },
  mutations: {
    "GET_LOADING":(state,payload)=>{
      state.loading=payload.loading
    },
    "SET_IMGFILE":(state,file)=>{//本地图片
      state.imgFile=file
    },
    "SET_CERTCARDMES":(state,mes)=>{//三要素的图片信息
      state.certCardMes=mes
    },
  },
  actions: {},
  modules: {
    
  }
});
