import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/face",
    name: "face",
    meta:{
      title: "刷脸身份认证",
      keepAlive:true,
    },
    component: () =>
      import(/* webpackChunkName: "face_recognition" */ "../views/face_recognition/index.vue")
  },
  {
    path: "/faceSuccess",
    name: "faceSuccess",
    meta:{
      title: "实名认证"
    },
    component: () =>
      import(/* webpackChunkName: "faceSuccess" */ "../views/faceSuccess/index.vue")
  },
  {
    path: "/addCar",
    name: "addCar",
    meta:{
      title: "添加车辆"
    },
    component: () =>
      import(/* webpackChunkName: "addCar" */ "../views/addCar/index.vue")
  },
  {
    path: "/ManualAudit",
    name: "ManualAudit",
    meta:{
      title: "信息验证"
    },
    component: () =>
      import(/* webpackChunkName: "ManualAudit" */ "../views/ManualAudit/index.vue")
  },
  {
    path: "/index",
    name: "index",
    meta:{
      title: "实名认证"
    },
    component: () =>
      import(/* webpackChunkName: "certification" */ "../views/certification/index.vue")
  },
  {
    path: "/faceIndex",
    name: "faceIndex",
    meta:{
      title: "实名认证",
    },
    component: () =>
      import(/* webpackChunkName: "faceIndex" */ "../views/faceIndex/index.vue")
  },
  {
    path: "/writeMes",
    name: "writeMes",
    meta:{
      title: "实名认证",
      clear:true,
    },
    component: () =>
      import(/* webpackChunkName: "writeMes" */ "../views/writeMes/index.vue")
  },
  {
    path: "/longTimeFace",
    name: "longTimeFace",
    meta:{
      title: "实名认证",
      clear:true,
    },
    component: () =>
      import(/* webpackChunkName: "longTimeFace" */ "../views/longTimeFace/index.vue")
  },
  {
    path: "/agent",
    name: "agent",
    meta:{
      title: "选择经销商",
      clear:true,
    },
    component: () =>
      import(/* webpackChunkName: "agent" */ "../views/agent/index.vue")
  },
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});
router.beforeEach((to, from, next)=>{
  if(to.meta.title){
    document.title=to.meta.title;
  }
  next()
})

export default router;
